.Logo{
    background-color: white;
    padding:8px;
    height: 100%;
    box-sizing: border-box;
    border-radius: 5px ;
}

.Logo img{
	height: 100%;
	
}



.Animate img{
    animation: float 6s ease-in-out infinite;
}

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-5px);
	}
	100% {
		transform: translatey(0px);
	}
}